<template>
  <div class="card mb-3 shadow" v-if="user.name">
    <div class="card-hearder"></div>
    <div class="card-body">
      <div class="form-group">
        <label for="title">Title:</label>
        <input
          class="form-control"
          type="text"
          id="title"
          v-model="newPost.title"
          autocomplete="off"
        >
        <label for="comment">Content:</label>
        <textarea
          id="contentInput"
          class="form-control"
          rows="7"
          v-model="newPost.content"
          @drop.prevent="onDrop($event);"
          @dragover.prevent="onDragover($event)"
          @dragleave.prevent="onDragend($event)"
          @dragend.prevent="onDragend($event)"
          @paste="pasteImage($event)"
        ></textarea>
      </div>
      <div class="images_holder">
        <p v-if="images.length">Total Uploaded Images: {{images.length}}</p>
        <div class="images-preview d-flex bg-light p-2" v-if="images.length">
          <div
            class="image_preview border mx-1 text-white p-3 font-weight-bold fa-2x"
            v-for="(i, index) in images"
            :key="index"
            :style="'background-image: url('+ i +')'"
            @click="addImage(i, index)"
            style="cursor:pointer"
            data-toggle="tooltip"
            data-placement="top"
            title="Add image to Post"
          >
            {{imageProgress[realImage[index].id] ? imageProgress[realImage[index].id] + '%' : ''}}
            <i
              class="fas fa-lg fa-times-circle bg-white rounded-circle text-dark"
              style="position: absolute; top: 5px;right: 5px;cursor: pointer;display:none"
              @click="removeImage(index)"
            ></i>
          </div>
        </div>
      </div>
    </div>
    <div class="card-footer">
      <div class="d-flex">
        <label
          for="imageUploader"
          class="btn btn-flat"
          data-toggle="tooltip"
          data-placement="top"
          title="Upload image"
        >
          <i class="fas fa-plus"></i>
          <input
            type="file"
            id="imageUploader"
            style="display:none"
            multiple
            @change="chooseImage($event)"
          >
        </label>
        <button
          class="btn ml-auto"
          v-bind:class="{'disabled': !newPost.title || isUploading}"
          @click="submitPost"
        >
          <i class="fas fa-spin fa-spinner" v-if="isUploading"></i> Post
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

import { CREATE_NEW_POST, FETCH_POST_LIST } from "../store/actions.type";
import { randomID } from "../utilities";

export default {
  name: "ActionForm",
  data() {
    return {
      newPost: {
        content: "",
        title: "",
        tag: [],
        images: []
      },
      realImage: [],
      images: [],
      imgURL: [],
      paths: [],
      filelimit: 20,
      hasImage: false,
      isUploading: false
    };
  },
  computed: {
    ...mapState({
      imgURLs: state => state.newPost.imgURL,
      img: state => state.newPost.img,
      imageProgress: state => state.newPost.imageProgress
    })
  },
  props: {
    user: Object
  },
  watch: {
    img(n, o) {
      console.log(n, o);
      if (n.length == this.images.length) {
        this.send(n);
        console.log(this.imageProgress);
      }
    }
  },
  methods: {
    submitPost() {
      if (this.isUploading == true) return;
      if (!this.newPost.title) return;
      if (this.realImage.length) {
        this.isUploading = true;
        let imageLength = this.paths.length;
        console.log(this.realImage, this.paths);
        for (let i = 0; i < imageLength; i++) {
          this.uploadImage(this.realImage[i], this.paths[i]);
        }
      } else if (this.newPost.title) {
        console.log(this.newPost)
        this.send();
      }
    },
    send(img) {
      let newPost = {
          content: this.newPost.content,
          title: this.newPost.title,
          tag: [],
          img: this.imgURLs
        },
        user = this.user,
        vm = this;
      
      if (this.newPost.title && img) {
        if (img.length) {
          this.$store.dispatch(CREATE_NEW_POST, { newPost, user }).then(() => {
            vm.isUploading = false;
          });
          this.newPost = {
            content: "",
            title: ""
          };
        }
      } else if (this.newPost.title) {
        console.log(newPost)
        this.$store.dispatch(CREATE_NEW_POST, { newPost, user }).then(() => {
          vm.isUploading = false;
        });
        this.newPost = {
          content: "",
          title: ""
        };
      }
      this.realImage = [];
      this.images = [];
      this.paths = [];
      this.imgURL = [];
    },
    uploadImage: async function(file, path) {
      // this.canSendMessage = false;
      this.$store.dispatch("UPLOAD_POST_IMAGE", {
        file: file,
        path: path
      });
      // let interval = setInterval(() => {
      // let count = this.$store.state.chat.fileCount;
      // this.imgURL = await this.$store.state.chat.images[id];
      // console.log(this.imgURL);
    },
    onPaste(e) {
      console.log(this.user);

      console.log(e);
      return true;
    },
    chooseImage(e) {
      let file = $("#imageUploader").get(0).files,
        vm = this,
        oversize = [],
        wrongtype = [];
      if (
        file.length > this.filelimit ||
        this.images.length + file.length > this.filelimit
      ) {
        vm.oversize(
          `LIMIT IMAGE IS ${this.filelimit}`,
          `Limit images per upload is ${this.filelimit}`
        );
      } else {
        for (let f of file) {
          if (f.size < 5000000) {
            if (
              f.type == "image/png" ||
              f.type == "image/jpeg" ||
              f.type == "image/gif"
            ) {
              f.id = randomID();
              this.realImage.push(f);
              this.images.push(window.URL.createObjectURL(f));
              let path = "posts" + "/" + Date.now() + "_" + f.name;
              this.paths.push(path);
            } else {
              wrongtype.push(f.name);
            }
          } else {
            oversize.push(f.name);
          }
        }
        if (oversize.length) {
          vm.oversize(
            "YOUR IMAGE IS TO BIG",
            `Try other image or reduce image size.\n 
          <p>Images: <code>${oversize}</code></p>`
          );
        }
        if (wrongtype.length) {
          vm.oversize(
            "YOUR FILES TYPE ARE NOT SUPPORTED",
            `Supported file types: <code>.png .jpg .gif</code>\n <p>Files: <code>${wrongtype}</code></p>`
          );
        }
      }
    },
    onDragover(e) {
      // this.isDragging = true;
      e.preventDefault();
      $("#contentInput").addClass("border border-primary");
      return false;
    },
    onDragend(e) {
      // this.isDragging = false;
      e.preventDefault();
      $("#contentInput").removeClass("border border-primary");
      return false;
    },
    onDrop(e) {
      // this.isDragging = false;
      e.stopPropagation();
      e.preventDefault();
      $("#contentInput").removeClass("border border-primary");

      var file = e.dataTransfer.files,
        vm = this,
        oversize = [],
        wrongtype = [];
      console.log(file);
      if (
        file.length > this.filelimit ||
        this.images.length + file.length > this.filelimit
      ) {
        vm.oversize(
          `LIMIT IMAGE IS ${this.filelimit}`,
          `Limit images per upload is ${this.filelimit}`
        );
      } else {
        for (let f of file) {
          if (f.size < 5000000) {
            if (
              f.type == "image/png" ||
              f.type == "image/jpeg" ||
              f.type == "image/gif"
            ) {
              f.id = randomID();
              this.realImage.push(f);
              this.images.push(window.URL.createObjectURL(f));
              let path =
                this.messages.groupId + "/" + Date.now() + "_" + f.name;
              this.paths.push(path);
            } else {
              wrongtype.push(f.name);
            }
          } else {
            oversize.push(f.name);
          }
        }
        if (oversize.length) {
          vm.oversize(
            "YOUR IMAGE IS TO BIG",
            `Try other image or reduce image size.\n 
          <p>Images: <code>${oversize}</code></p>`
          );
        }
        if (wrongtype.length) {
          vm.oversize(
            "YOUR FILES TYPE ARE NOT SUPPORTED",
            `Supported file types: <code>.png .jpg .gif</code>\n <p>Files: <code>${wrongtype}</code></p>`
          );
        }
      }
      return false;
    },
    pasteImage(e) {
      e.preventDefault();
      let file = e.clipboardData.files;
      if (file.length) {
        let path = "posts" + "/" + Date.now() + "_" + file[0].name;
        file[0].id = randomID();
        this.paths.push(path);
        this.images.push(window.URL.createObjectURL(file[0]));
        this.realImage.push(file[0]);
      }
      let text = (e.originalEvent || e).clipboardData.getData("text/plain");
      document.execCommand("insertHTML", false, text);
    },
    removeImage(k) {
      this.images.splice(k, 1);
      this.realImage.splice(k, 1);
      window.URL.revokeObjectURL(this.realImage.splice(k, 1));
      this.paths.splice(k, 1);
      if (this.images.length == 0) this.hasImage = false;
    },
    oversize(title, text) {
      this.$modal.show("dialog", {
        title,
        text,
        buttons: [
          {
            title: "CLOSE",
            default: false
          }
        ]
      });
    },
    addImage(i, index) {
      let pos = $("#contentInput").prop("selectionStart"),
        img = ` <<I${this.realImage[index].id}>> `;
      var output = [
        this.newPost.content.slice(0, pos),
        img,
        this.newPost.content.slice(pos)
      ].join("");
      this.newPost.content = output;
    }
  },
  mounted() {
    $('[data-toggle="tooltip"]').tooltip();
    // this.$store.dispatch('delete');
  }
};
</script>

<style scoped>
textarea {
  resize: none;
}
.clickable {
  cursor: not-allowed;
}
.image_preview {
  /* width: 100%; */
  height: 100%;
  background: no-repeat center;
  background-size: cover;
  position: relative;
  z-index: 3;
  min-width: 100px;
}
.images-preview {
  height: 100px;
  z-index: 2;
  overflow: auto hidden;
}
.image_preview:hover i {
  display: block !important;
}
</style>



